@use '../../../../styles/mixins.scss' as *;

.top {
  position: relative;
  &__description {
    position: absolute;
    background: black;
    width: 55%;
    bottom: 1rem;
    right: 0;
    opacity: 0.7;
    padding: 1rem;
    z-index: 1;
    &__title {
      text-align: center;
      font-size: 18px;
      color: #fff;
      cursor: pointer;
      & > p {
        font-size: 30px;
        font-weight: 900;
      }
      &__info {
        display: flex;
        justify-content: center;
        align-items: center;
        & > p {
          margin: 0.5rem;
        }
      }
    }
    &__more {
      color: rgb(255, 196, 0);
    }
  }
  &__img {
    width: 950px;
    @include responsive {
      width: 420px;
    }
    animation: fadeImage 2s forwards;
    -webkit-animation: fadeImage 2s forwards;
    -o-animation: fadeImage 2s forwards;
    -moz-animation: fadeImage 2s forwards;
  }
}


@keyframes fadeImage {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}